.edit {
  outline: 2px solid hsla(0, 0%, 100%, 0.1) !important;
  position: relative;
  cursor: pointer;
  min-height: 20px;
  min-width: 18px;
  display: inline-block;
}
.edit .pencil {
  display: none;
}
.edit:focus,
.edit:hover {
  background-color: rgba(0, 0, 0, 0.1) !important;
  outline: 2px solid hsla(0, 0%, 100%, 0.5) !important;
}
.edit:focus .pencil,
.edit:hover .pencil {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 24px;
}
.edit:focus .pencil svg,
.edit:hover .pencil svg {
  width: 14px;
  height: 14px;
  background: #fff;
  opacity: 0.7;
  padding: 5px;
  box-sizing: content-box;
}
.edit.hasNoWrapper {
  font-size: inherit !important;
}
